import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import states from "../api/states";
import PageSpinner from 'components/PageSpinner';
import { findExpGroups, findTrucks, findExpenses, newExpense, findBillingStatus} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import CustomToolbarSelectExpenses from "../components/CustomToolbarSelectExpenses";
class FormPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            type : 'nn',
            truck : 'nn',
            amount : '',
            description:'',
            installment:'',
            frequency:'nn',
            city:'',
            state:'nn',
            misc_amount:'',
            xtoken:'',
            msg: '',
            error: '',
            downloadFile:true,
            preload:false,
            showmore:true,
            data: [],
            trucks:[],
            groups:[],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findBillingStatus(obje_results.user.token).then((response) => {
              if( parseInt(response.expired) === 1 )
              {
                window.location.href = '/bills/payment';
              }
            }).catch((er) => console.log(er));
            findExpenses(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    data:response.data,
                });
            }).catch((respError) => {});
            
            findTrucks(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    trucks:response.data,
                });
            }).catch((respError) => {});

            findExpGroups(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    groups:response.data,
                });
            }).catch((respError) => {});

          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    isValidEmailAddress = (email) => {
        if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            return true;
        }
        return false;
    }

    firstUpper = (str) => {
        let string = str.toLowerCase();
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    rerenderParentCallback = () => {
      findExpenses(this.state.xtoken).then((response) => {
        this.setState({
            ...this.state,
            data:response.data,
        });
      }).catch((respError) => {
      });
    }
    
    handleTypeInput = (event) => {
        const v = event.target.value;
        if( parseInt(v) === 1)
        {
            this.setState({...this.state, type: v, showmore:true});
        }
        else if( parseInt(v) > 1 )
        {
            this.setState({...this.state, type: v, showmore:false});
        }
    }
    handleTruckInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, truck: v,});
    }
    handleAmountInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, amount: v,});
    }
    handleDescriptionInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, description: v,});
    }
    handleInstallmentInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, installment: v,});
    }
    handleFrequencyInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, frequency: v,});
    }
    handleCityInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, city: v,});
    }
    handleStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleMscAmountInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, misc_amount: v,});
    }
    
    handleSaveChanges = () => {
        this.setState({ ...this.state, preload:true});
        newExpense(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    type : 'nn',
                    truck : 'nn',
                    amount : '',
                    description:'',
                    installment:'',
                    frequency:'nn',
                    city:'',
                    state:'nn',
                    misc_amount:'',
                    msg: response.message,
                    data: response.data,
                    preload:false,
                    error: '',
                });
                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    preload:false,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
                preload:false,
            });
            console.log('error', error);
        });
    }
    render() {
        const columns = [
            {
              name: 'Id',
              options: {
                display: false
              }
            },
            {
                name: 'Gid',
                options: {
                  display: 'excluded',
                }
            },
            {
                name: 'Tid',
                options: {
                  display: 'excluded',
                }
            },
            'Description',
            'Truck',
            {
              name: 'Amount',
              options: {
                display:true,
                customBodyRender: (value, tableMeta, updateValue) => (
                    <Label>${value}</Label>
                )
              }
            },
            {
              name: 'Group',
              options: {
                display: false
              }
            },
            {
              name: 'Installment',
              options: {
                display:true,
                customBodyRender: (value, tableMeta, updateValue) => (
                    <Label>${value}</Label>
                )
              }
            },
            {
              name: 'Frequency',
              options: {
                display: true,
                customBodyRender: (value, tableMeta, updateValue) => (
                    <Label>{value}</Label>
                )
              },
            },
            {
              name: 'City',
              options: {
                display: true,
              },
            },
            {
              name: 'State',
              options: {
                  display: true,
              },
            },
            {
                name: 'Msc. Amount',
                options: {
                    display: true,
                },
            },
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.id,
              item.type,
              item.truck,
              item.description,
              item.tlabel,
              item.amount,
              item.glabel,
              item.installment,
              item.frequency,
              item.city,
              item.state,
              item.misc_amount,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'single',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
              <CustomToolbarSelectExpenses rerenderParentCallback={this.rerenderParentCallback} selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows}  originalData={this.state.data} Xtoken={this.state.xtoken}/>
            ),
            downloadOptions: {
                filename: 'all-expenses-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Expenses" breadcrumbs={[{ name: 'Expenses', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>
                    Create expense
                    {this.state.preload && (<PageSpinner/>)}
                </CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">Expense type*</Label>
                      <Input onChange={this.handleTypeInput} value={this.state.type} defaultValue={this.state.type} type="select" name="type">
                        <option value="nn" disabled={true}>Select type</option>
                        {
                            this.state.groups.map((stt, idx) => (
                                <option key={idx} value={stt.id}>{stt.name}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">Related truck*</Label>
                      <Input onChange={this.handleTruckInput} value={this.state.truck} defaultValue={this.state.truck} type="select" name="truck">
                        <option value="nn" disabled={true}>Select truck</option>
                        {
                            this.state.trucks.map((stt, idx) => (
                                <option key={idx} value={stt.id}>{this.firstUpper(stt.make) + " - " + stt.number}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Full deduction amount($)*</Label>
                      <Input onChange={this.handleAmountInput}
                        type="number" 
                        value={this.state.amount}
                        name="amt"
                        placeholder=""
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="examplePassword">Description*</Label>
                      <Input onChange={this.handleDescriptionInput}
                        type="text"
                        value={this.state.description}
                        name="origin"
                        placeholder=""
                      />
                    </FormGroup>
                    {this.state.showmore && (
                        <div>
                            <FormGroup>
                                <Label for="exampleEmail">Installment($)*</Label>
                                <Input onChange={this.handleInstallmentInput}
                                type="number" 
                                value={this.state.installment}
                                name="installment"
                                placeholder=""
                                />
                            </FormGroup>
                        </div>
                    )}
                    { !this.state.showmore && (
                        <div>
                            <FormGroup>
                                <Label for="exampleNumber">City*</Label>
                                <Input onChange={this.handleCityInput}
                                    type="text"
                                    value={this.state.city}
                                    name="rate"
                                    id="rate"
                                    placeholder=""
                                />
                            </FormGroup>
                        </div>
                    )}
                  </Form>
                </CardBody>
              </Card>
            </Col>
    
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    { this.state.showmore && (
                        <div>
                        <FormGroup>
                        <Label for="exampleSelect">Frequency*</Label>
                        <Input onChange={this.handleFrequencyInput} value={this.state.frequency} defaultValue={this.state.frequency} type="select" name="freq">
                            <option value="nn" disabled={true}>Select frequency</option>
                            <option value="Weekly">Weekly</option>
                            <option value="Monthily">Monthily</option>
                        </Input>
                        </FormGroup>
                        </div>
                    )}
                    { !this.state.showmore && (
                        <div>
                          <FormGroup>
                            <Label for="exampleSelect">State*</Label>
                            <Input onChange={this.handleStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                                <option value="nn" disabled={true}>Select state</option>
                                {
                                  states.data.map((stt, idx) => (
                                    <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                                  ))
                                }
                            </Input>
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleNumber">Misc. Amount($)*</Label>
                            <Input onChange={this.handleMscAmountInput}
                            type="number"
                            value={this.state.misc_amount}
                            name="misc"
                            id="misc"
                            placeholder=""
                            />
                        </FormGroup>
                        </div>
                    )}
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Add expense</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
        </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Expenses'}
                    data={this.state.data.map(item => {
                      return [
                        item.id,
                        item.type,
                        item.truck,
                        item.description,
                        item.tlabel,
                        item.amount,
                        item.glabel,
                        item.installment,
                        item.frequency,
                        item.city,
                        item.state,
                        item.misc_amount,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default FormPage;
